import React, { useEffect, useState } from 'react';
import { Modal, Button, Input, Cascader, notification } from 'antd';
import {
    UserOutlined, UnlockTwoTone, BankTwoTone,
    EnvironmentTwoTone, PhoneTwoTone, MailTwoTone
} from '@ant-design/icons'
import { useMutation } from '@apollo/client';
import { CREATE_USER } from '../gql';
import { validatePassword, validateUserName } from '../../commonFunction/validation';
import { placesInitialization } from '../../datainitialization/place';

const ModalRegister = ({ props }) => {
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [currentStep, setCurrentStep] = useState(1)
    const [inforegister, setInfoRegister] = useState({})
    const [createUser] = useMutation(CREATE_USER)

    useEffect(() => {
        setInfoRegister({})
    }, [])

    const showModal = () => {
        setIsModalVisible(true)
        setCurrentStep(1)
    }

    const handleClickNextStep = () => {
        try {
            if (!inforegister.userName || !inforegister.password) return notification.error({
                message: 'Tn ng nhp',
                description:
                    'Tn ng nhp hoc mt khu khng  trng',
                placement: 'bottomRight'
            })
            if (!validatePassword(inforegister.password)) return notification.error({
                message: 'Mt khu',
                description:
                    'Mt khu khng hp l',
                placement: 'bottomRight'
            })

            if (!validateUserName(inforegister.userName)) return notification.error({
                message: 'Tn ng nhp',
                description:
                    'Tn ng nhp khng hp l',
                placement: 'bottomRight'
            })

            if (currentStep < 3) {
                setCurrentStep(currentStep + 1)
            } else {
                createUser({variables: { input: { ...inforegister, name: "" } } }).then(p => {
                    if (p.data && p.data.createUser) {
                        localStorage.setItem('token', p.data.createUser);
                        props.setLogin(true)
                        notification.success({
                            message: 'To ti khon thnh cng',
                            description: 'Thnh cng',
                            placement: 'bottomRight'
                        })
                    }
                })
            }
        } catch (error) {
            return notification.error({
                message: 'Li mng',
                description:
                    'Li mng',
                placement: 'bottomRight'
            })
        }

    }

    const handleOnchangeRegister = (key, { target: { value } }) => {
        inforegister[key] = value
    }

    const handleOnchangeCascader = (value) => {
        inforegister.codePlace = value[2]
    }
    const handleCancel = () => {
        setIsModalVisible(false);
    }

    return (
        <>
            <Button
                type="primary"
                onClick={showModal}
                shape="round"
                style={{
                    width: "80%",
                    background: "green",
                    color: "white",
                    height: "48px",
                    fontSize: '18px'
                }
                }
            >
                To Ti Khon Mi
            </Button>
            <Modal title={
                currentStep === 1
                    ? "Thng tin ng nhp" :
                    (currentStep === 2 ? 'Thng tin nh thuc' : 'Thng tin khc')}
                style={{ top: 10 }}
                footer={null} visible={isModalVisible}
                closable={false}
            // onCancel={handleCancel}
            >
                <div style={{ display: currentStep === 1 ? 'block' : 'none' }}>
                    <p>in vo tn ng nhp</p>
                    <Input placeholder="Tn ng nhp"
                        prefix={<UserOutlined />}
                        onChange={(value) => handleOnchangeRegister('userName', value)}
                    />
                    <p style={{ marginTop: "16px" }} > in vo mt khu ca bn Ti thiu 6 k t</p>
                    <Input.Password placeholder="Mt khu"
                        prefix={<UnlockTwoTone />}
                        onChange={(value) => handleOnchangeRegister('password', value)}
                    />
                </div>
                <div style={{ display: currentStep === 2 ? 'block' : 'none' }}>
                    <p><strong>in vo tn nh thuc ca bn</strong></p>
                    <Input placeholder="Tn Nh thuc"
                        prefix={<BankTwoTone />}
                        onChange={(value) => handleOnchangeRegister('nodeName', value)}
                    />
                    <p><strong>a ch nh thuc ca bn:</strong> </p>
                    <Input placeholder="S nh"
                        prefix={<EnvironmentTwoTone />}
                        onChange={(value) => handleOnchangeRegister('addressDetail', value)}
                    />
                    <p><strong>Tnh / Huyn / X</strong> </p>
                    <Cascader size="small" style={{ width: '100%', fontSize: '12px' }}
                        options={placesInitialization.data.getPlacesBasic}
                        placeholder="Chn a ch"
                        onChange={(value) => handleOnchangeCascader(value)}
                    />
                </div>
                <div style={{ display: currentStep === 3 ? 'block' : 'none' }}>
                    <p><strong>S in thoi ca bn</strong></p>
                    <Input
                        placeholder="S in thoi"
                        prefix={<PhoneTwoTone />}
                        onChange={(value) => handleOnchangeRegister('mobile', value)}
                    />
                    <p><strong>Email ca bn</strong></p>
                    <Input placeholder="Email"
                        prefix={<MailTwoTone />}
                        onChange={(value) => handleOnchangeRegister('email', value)}
                    />
                </div>
                <Button style={{ width: '100%', height: "36px", marginTop: "20px" }}
                    type="primary" onClick={handleClickNextStep}>
                    {currentStep === 3 ? 'ng K' : 'Bc tip theo'}
                </Button>
                <Button onClick={handleCancel}>  ng </Button>
            </Modal>
        </>
    )
}
export default ModalRegister